export default {
  data() {
    return {
      memberList: [],
      filterTxt: ''
    };
  },
  computed: {
    filerMemberData() {
      let filerMemberList = this.memberList;
      if (this.filterTxt === '') {
        return filerMemberList;
      }
      return filerMemberList = this.memberList.filter(member => member.name.toLowerCase().match(this.filterTxt.toLowerCase()) || member.cellphone.toLowerCase().match(this.filterTxt.toLowerCase()) || member.email.toLowerCase().match(this.filterTxt.toLowerCase()));
    }
  },
  created() {
    this.getMemberListData();
  },
  methods: {
    goTop() {
      $('html,body').scrollTop(0, 0);
    },
    goTopScroll() {
      $('html,body').animate({
        scrollTop: (0, 0)
      }, 'slow');
    },
    getMemberListData() {
      this.$store.dispatch('updateIsShowLoading', 0);
      const showMemberListApi = process.env.VUE_APP_A_SHOW_MEMBER_INFO;
      this.axios.post(showMemberListApi).then(response => {
        this.$store.dispatch('updateIsShowLoading', 2);
        const backData = response.data.result;
        this.memberList = backData;
      }).catch(function (error) {
        console.log('error', error);
      });
    }
  }
};